.cc-button {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 37px;
  color: white;
  font-size: 16px;
  border: 1px solid #9b9b9b;
  margin-top: 16px;
  cursor: pointer;
  width: 211px;
  height: 46px;
}

.cc-button.error {
  background-color: unset;
  color: var(--error-color);
  border-color: var(--error-color);
  pointer-events: none;
}

.cc-button.success {
  color: var(--green);
  background-color: white;
  border-color: var(--green);
}

.cc-button.disable {
  background-color: #9b9b9b;
  pointer-events: none;
  border: none;
  color: white;
}

.cc-button.activated {
  pointer-events: none;
}

.cc-button.try-again,
.cc-button.deactivate {
  background-color: unset;
  border: unset;
  color: var(--gray);
  text-decoration: underline;
  padding: 14px 24px;
  width: unset;
}

.cc-button-wrapper {
  display: inline;
}

.cc-button-wrapper.loading {
  pointer-events: none;
}
