@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

:root {
  --cc-base-color: #fc6723;
  --ce-base-color: #e12468;

  --cc-base-light: #fff3f0;
  --ce-base-light: #f3a7c3;

  --main-gray: #d5d5d5;
  --secondary-gray: #f4f4f4;
  --red: #ff4747;
  --secondary-red: #fcdbdb;
  --green: #28b158;
  --error-color: #ff1300;
  --gray: #9b9b9b;
}
.cc-color {
  color: var(--cc-base-color);
}
.ce-color {
  color: var(--ce-base-color);
}
.ce-color-svg svg path {
  fill: var(--ce-base-color);
}
.cc-color-svg svg path {
  fill: var(--cc-base-color);
}

.ce-background-color {
  background-color: var(--ce-base-color);
}
.cc-background-color {
  background-color: var(--cc-base-color);
}

.paste-all .ce-color svg path {
  fill: var(--ce-base-color);
}
.paste-all .cc-color svg path {
  fill: var(--cc-base-color);
}

#wp-cc-plugin {
  background-color: #ffffff;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: "poppins", sans-serif;
}
.border {
  border: 1px solid var(--main-gray);
}

.main-header {
  border: 1px solid var(--main-gray);
  margin-bottom: 24px;
}

.sub-header {
  display: flex;
  align-items: center;
}

.logo {
  margin: 40px 0px 40px 52px;
}

.install-plugin {
  display: flex;
  align-items: center;
}

.content {
  width: 1016px;
  margin: 0 auto;
  margin-bottom: 41px;
}
.install-plugin .check-mark-circle {
  margin: 32px 0 36px 65px;
  vertical-align: middle;
}

.install-plugin-low .check-mark-circle {
  margin: 40px 0 43px 65px;
  vertical-align: middle;
}

.empty-circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid var(--main-gray);
}
.display-inline-block {
  display: inline-block;
}
.display-block {
  display: block;
}
.empty-circle-text {
  font-size: 16px;
}
.section-body {
  margin: 0 88px 32px 97px;
}
.key-input {
  width: 100%;
  height: 48px;
  border-radius: 7px;
  border: 1px solid var(--gray);
  padding-left: 16px;
}
.input-icon {
  position: absolute;
  right: 16px;
  cursor: pointer;
}
.position-relative {
  position: relative;
}

.disable {
  background-color: var(--gray);
  pointer-events: none;
}
.enable {
  background-color: #fc6723;
}

.tracking-code-desc {
  margin: 9px 0 16px 0;
  font-size: 14px;
}

header {
  height: 142px;
}
.input-title {
  font-weight: 400;
  font-size: 14px;
}
input:focus::placeholder {
  color: transparent;
}

.install-tracking-code {
  cursor: pointer;
  text-decoration: underline;
  font-size: 14px;
  font-weight: 400;
  margin: 16px 0px 32px 97px;
}
.alert-remove {
  display: inline-block;
  float: right;
  margin-right: 88px;
  font-weight: 400;
  font-size: 14px;
}
.alert {
  color: var(--green);
}
.remove {
  color: var(--gray);
  cursor: pointer;
  text-decoration: underline;
  display: inline-block;
  margin-left: 10px;
}

.success-msg {
  color: var(--green);
}
. {
  font-weight: 400;
  font-size: 12px;
  margin-top: 10px;
}
.protected {
  font-size: 14px;
  font-weight: 400;
  margin: 16px 0px 32px 97px;
}
.auth-button-text {
  padding-left: 8px;
}
.cursor-not-allowed {
  cursor: not-allowed;
}

.input-wrapper {
  position: relative;
  display: flex;
  width: 100%;
  align-items: center;
  margin-top: 8px;
  margin-bottom: 16px;
}

.input-wrapper > .cc-input {
  display: flex;
  flex-grow: 1;
  position: relative;
  align-items: center;
}

.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  transition: opacity 200ms;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}

.cc-flex-column {
  display: flex;
  flex-direction: column;
}

.cc-flex {
  display: flex;
}
.cheq-essential-header {
  background: linear-gradient(
    109.72deg,
    #441c74 5.34%,
    #5a297e 53.97%,
    #81267a 74.72%,
    #bc2075 91.81%,
    #f31d7e 100.06%
  );
}

.cheq-essential-header svg {
  padding: 50px;
}
